import pdfUrl from './AGR010  Pantallas térmicas en invernaderos.pdf';

import React, { useEffect, useState } from "react";
import axios from "axios";
import * as pdfjsLib from "pdfjs-dist";
import { PDFDocument } from "pdf-lib";
import { useAuthState } from "react-firebase-hooks/auth";
import { auth } from "../../components/firebase/firebaseConfig";
import {
  IconButton,
  Typography,
  CircularProgress,
} from "@mui/material";
import { toast, ToastContainer } from "react-toastify";
import { Tune as TuneIcon, Download as DownloadIcon } from "@mui/icons-material";
import "react-toastify/dist/ReactToastify.css";
import { useNavigate } from "react-router-dom";
import ExcelUploaderStorage from "./zExcelUploaderStoragePrecargaxDefecto";

import config from '../../components/configURL';
// Configuración de pdf.worker
pdfjsLib.GlobalWorkerOptions.workerSrc = `/workers/3.11.174/pdf.worker.min.js`;
const ENDPOINT = "numero"
const PDF_API_URL = `${config.API_URL}/${ENDPOINT}?timestamp=${new Date().getTime()}`;

const PDFRenderer = () => {
  const [user] = useAuthState(auth);
  const navigate = useNavigate();
  const [images, setImages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [formData, setFormData] = useState({});
  const [dataFromApi, setDataFromApi] = useState(null); // State to store API data

  // Function to calculate form values dynamically based on API data
  const calculados = (data) => ({
    "S": data?.S || "44",  // Default values if data is not available
    "Ki": data?.Ki || "44",
    "Kp": data?.Kp || "22",
  });

  // Fetch data from sessionStorage or API
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(null);

      // Get data from sessionStorage
      const sessionData = sessionStorage.getItem("excelData");

      // Use example data if no session data is found
      const dataToSend = sessionData ? JSON.parse(sessionData) : { example: "data" };

      try {
        // Fetch data from the API
        const response = await axios.post(PDF_API_URL, dataToSend);

        // Ensure API response is valid
        if (response.data) {
          setDataFromApi(response.data); // Save the API response
        }
      } catch (error) {
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []); // Empty dependency array ensures this only runs on component mount

  // Fetch the PDF and fill in the form
  const fetchPdf = async (pdfUrl) => {
    try {
      return await fetch(pdfUrl).then((res) => res.arrayBuffer());
    } catch (err) {
      console.error("Error fetching PDF:", err);
      throw new Error("Failed to fetch PDF.");
    }
  };

  // Fill the form fields in the PDF with dynamic data from API
  const fillPdfFields = async (pdfBytes) => {
    try {
      const pdfDoc = await PDFDocument.load(pdfBytes);
      const form = pdfDoc.getForm();

      // Only fill the PDF if the data is available
      if (dataFromApi) {
        const updatedData = calculados(dataFromApi);

        // Fill in the PDF form fields with the dynamic data
        Object.entries(updatedData).forEach(([name, value]) => {
          try {
            const field = form.getField(name);
            field.setText(String(value));
          } catch (err) {
            console.warn(`Field '${name}' not found in PDF:`, err.message);
          }
        });
      }

      return await pdfDoc.save();
    } catch (err) {
      console.error("Error filling PDF fields:", err);
      throw new Error("Failed to fill PDF fields.");
    }
  };

  // Render PDF as images
  const renderPdfAsImages = async (pdfBytes) => {
    try {
      const pdf = await pdfjsLib.getDocument({ data: pdfBytes }).promise;
      const pages = [];

      for (let i = 1; i <= pdf.numPages; i++) {
        const page = await pdf.getPage(i);
        const viewport = page.getViewport({ scale: 2.5 });
        const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d");
        canvas.width = viewport.width;
        canvas.height = viewport.height;
        await page.render({ canvasContext: context, viewport }).promise;
        pages.push(canvas.toDataURL("image/png"));
      }

      setImages(pages);
    } catch (err) {
      console.error("Error rendering PDF as images:", err);
      setError("Failed to render PDF.");
    }
  };

  // Handle downloading the modified PDF
  const handleDownload = async () => {
    if (!user) return toast.error("Debe iniciar sesión");

    try {
      const pdfBytes = await fetchPdf(pdfUrl); // Replace with your actual PDF URL
      const modifiedPdfBytes = await fillPdfFields(pdfBytes);
      const blob = new Blob([modifiedPdfBytes], { type: "application/pdf" });
      const link = document.createElement("a");
      link.href = URL.createObjectURL(blob);
      link.download = "modified-pdf.pdf";
      link.click();
    } catch (err) {
      console.error("Error downloading PDF:", err);
    }
  };

  // Only start fetching the PDF once the API data is available
  useEffect(() => {
    const initializePdf = async () => {
      if (dataFromApi) {  // Check if data is available before processing PDF
        try {
          setLoading(true);
          const response = await fetch(pdfUrl); // Replace with your actual PDF URL
          if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
          const pdfBytes = await response.arrayBuffer();
          const modifiedPdfBytes = await fillPdfFields(pdfBytes);
          await renderPdfAsImages(modifiedPdfBytes);
        } catch (err) {
          console.error("Initialization error:", err);
          setError("No se pudo inicializar el PDF.");
        } finally {
          setLoading(false);
        }
      }
    };

    initializePdf();
  }, [dataFromApi]); // Trigger this effect only when dataFromApi changes

  // Button handlers for modal
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setOpen(false);
    window.location.reload();
  };

  return (
    <div>
      <IconButton
        onClick={handleOpen}
        color="success"
        style={{
          position: 'fixed',
          top: '50%',
          right: '20px',
          backgroundColor: 'rgba(255, 255, 255, 0.5)',
        }}
      >
        <TuneIcon />
      </IconButton>

      {user && (
        <IconButton
          onClick={handleDownload}
          color="success"
          style={{
            position: 'fixed',
            top: '60%',
            right: '20px',
            backgroundColor: 'rgba(255, 255, 255, 0.5)',
          }}
        >
          <DownloadIcon />
        </IconButton>
      )}

      {loading && !dataFromApi && <CircularProgress style={{ position: "absolute", top: "50%", left: "50%", transform: "translate(-50%, -50%)" }} />}
      {error && <Typography color="error">{error}</Typography>}

      {/* Render API data (if available) */}
      {dataFromApi && (
        <div>
          <Typography variant="h6">Datos de la API:</Typography>
          <pre>{JSON.stringify(dataFromApi, null, 2)}</pre>
        </div>
      )}

      {/* Render PDF images after the API data has been loaded */}
      {images.length > 0 && (
        <div>
          {images.map((src, index) => (
            <img key={index} src={src} alt={`Page ${index + 1}`} style={{ width: "100%", borderRadius: "8px" }} />
          ))}
        </div>
      )}

      <ToastContainer />
      <ExcelUploaderStorage open={open} handleClose={handleClose} />
    </div>
  );
};

export default PDFRenderer;
