import React, { useState, useEffect } from "react";
import {
    Button,
    Box,
    Typography,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Link,
    Collapse,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    IconButton,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ExpandLessIcon from "@mui/icons-material/ExpandLess";
import { useNavigate } from "react-router-dom";
import * as XLSX from "xlsx";

const ExcelUploaderStorage = ({ open, handleClose }) => {
    const [sheets, setSheets] = useState(null); // Datos finales confirmados
    const [tempSheets, setTempSheets] = useState(null); // Datos cargados temporalmente
    const [dragOver, setDragOver] = useState(false);
    const [expanded, setExpanded] = useState({}); // Estado para controlar expansión de hojas

    useEffect(() => {
        const storedData = sessionStorage.getItem("excelData");
        if (storedData) {
            setSheets(JSON.parse(storedData));
        }
    }, []);

    const handleFileUpload = (file) => {
        if (!file) return;

        const reader = new FileReader();
        reader.onload = (e) => {
            const data = new Uint8Array(e.target.result);
            const workbook = XLSX.read(data, { type: "array" });
            const sheetsData = {};

            workbook.SheetNames.forEach((sheetName) => {
                const sheet = XLSX.utils.sheet_to_json(workbook.Sheets[sheetName], {
                    header: 1,
                });

                const filteredSheet = sheet.filter((row) =>
                    row.some((cell) => cell !== null && cell !== undefined && cell !== "")
                );

                sheetsData[sheetName] = filteredSheet;
            });

            setTempSheets(sheetsData);
        };

        reader.readAsArrayBuffer(file);
    };

    const handleInputChange = (event) => {
        const file = event.target.files[0];
        handleFileUpload(file);
    };

    const handleDragOver = (event) => {
        event.preventDefault();
        setDragOver(true);
    };

    const handleDragLeave = () => {
        setDragOver(false);
    };

    const handleDrop = (event) => {
        event.preventDefault();
        setDragOver(false);
        const file = event.dataTransfer.files[0];
        handleFileUpload(file);
    };

    const navigate = useNavigate();

    const handleConfirm = () => {
        if (tempSheets) {
            setSheets(tempSheets);
            sessionStorage.setItem("excelData", JSON.stringify(tempSheets));
        }
        setTempSheets(null);
        handleClose();
        navigate(0);
    };

    const handleCancel = () => {
        setTempSheets(null);
        handleClose();
    };

    const toggleExpand = (sheetName) => {
        setExpanded((prev) => ({
            ...prev,
            [sheetName]: !prev[sheetName],
        }));
    };

    return (
        <Box sx={{ p: 2 }}>
            {/* <Dialog open={open} onClose={handleClose} fullWidth maxWidth="sm"> */}
            <Dialog open={open} onClose={handleClose} fullWidth fullScreen>

                <DialogTitle>Subir archivo Excel de parámetros</DialogTitle>
                <DialogContent>
                    <Box
                        sx={{
                            mt: 2,
                            p: 2,
                            border: "2px dashed",
                            borderColor: dragOver ? "primary.main" : "grey.400",
                            borderRadius: 2,
                            textAlign: "center",
                            backgroundColor: dragOver ? "grey.100" : "transparent",
                        }}
                        onDragOver={handleDragOver}
                        onDragLeave={handleDragLeave}
                        onDrop={handleDrop}
                    >
                        <Typography variant="body1" gutterBottom>
                            Arrastra y suelta tu archivo aquí o selecciona uno manualmente
                        </Typography>
                        <Button variant="contained" component="label">
                            Seleccionar Archivo
                            <input
                                type="file"
                                hidden
                                accept=".xlsx, .xls"
                                onChange={handleInputChange}
                            />
                        </Button>
                    </Box>
                    <Box sx={{ mt: 2 }}>
                        <Typography variant="body2">
                            ¿Necesitas una plantilla? Descárgala haciendo clic
                            <Link href="/plantillasParametros/caes.xlsx" download sx={{ ml: 1 }}>
                                aquí
                            </Link>
                            .
                        </Typography>
                    </Box>
                    {tempSheets && (
                        <Box sx={{ mt: 2 }}>
                            <Typography variant="subtitle1">Hojas procesadas:</Typography>
                            {Object.keys(tempSheets).map((sheetName) => (
                                <Box key={sheetName} sx={{ mt: 1 }}>
                                    <Box
                                        sx={{
                                            display: "flex",
                                            alignItems: "center",
                                            justifyContent: "space-between",
                                        }}
                                    >
                                        <Typography variant="body1">{sheetName}</Typography>
                                        <IconButton onClick={() => toggleExpand(sheetName)}>
                                            {expanded[sheetName] ? (
                                                <ExpandLessIcon />
                                            ) : (
                                                <ExpandMoreIcon />
                                            )}
                                        </IconButton>
                                    </Box>
                                    <Collapse in={expanded[sheetName]} timeout="auto" unmountOnExit>
                                        <TableContainer component={Paper} sx={{ mt: 1 }}>
                                            <Table size="small">
                                                <TableHead>
                                                    <TableRow>
                                                        {tempSheets[sheetName][0]?.map((cell, index) => (
                                                            <TableCell key={index}>{cell}</TableCell>
                                                        ))}
                                                    </TableRow>
                                                </TableHead>
                                                <TableBody>
                                                    {tempSheets[sheetName].slice(1).map((row, rowIndex) => (
                                                        <TableRow key={rowIndex}>
                                                            {row.map((cell, cellIndex) => (
                                                                <TableCell key={cellIndex}>{cell}</TableCell>
                                                            ))}
                                                        </TableRow>
                                                    ))}
                                                </TableBody>
                                            </Table>
                                        </TableContainer>
                                    </Collapse>
                                </Box>
                            ))}
                        </Box>
                    )}
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCancel} color="secondary">
                        Cancelar
                    </Button>
                    <Button onClick={handleConfirm} color="primary">
                        Confirmar
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default ExcelUploaderStorage;



